.personal-info-list {
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 16px;
  max-width: 250px;
  background-color: #f8f9fa;
}

.personal-info-list h3 {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 16px;
  font-weight: 600;
}

.personal-info-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.personal-info-item {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.personal-info-item .icon {
